body {
    background: linear-gradient(to bottom, #b0e0e6, #87cefa); /* Gradient background color for the whole page */
    margin: 0;
    font-family: 'Arial', sans-serif; /* Default font */
    color: #000000; /* Default text color */
}
  
  .card {
    transition: transform 0.3s ease-in-out;
    height: 20em;
    max-width: 400px;
    margin: 0 auto;
    background-color: #3498db; /* Background color for the form */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    color:#fff;
  }
  
  .card:hover {
    transform: translateY(-10px); /* Floating effect */
    box-shadow: 0 5px 10px rgba(45, 35, 131, 0.5); /* Subtle box shadow on hover */
  }
  
  /* Centering styles for headings and paragraphs */
  h1, h2, p {
    text-align: center; /* Center the text */
  }
  
  /* Styling for h1 */
  h1 {
    font-family: 'Bebas Neue', sans-serif;
    font-size: 2.5em;
    color: #000000;
    margin-bottom: 10px;
  }
  
  /* Styling for h2 */
  h2 {
    font-family: 'Bebas Neue', sans-serif;
    font-size: 2em;
    color: #000000;
    margin-bottom: 8px;
  }
  
  /* Styling for p */
  p {
    font-size: 1em;
    line-height: 1.6;
    margin-bottom: 16px;
  }
  
  input, textarea {
    background: linear-gradient(to bottom, #ffffff, #84bce4); /* Gradient background color for form elements */
    padding: 20px;
    text-align: center;
    border: none;
    border-radius: 5px;
  }
  
  #contact, #preface, #about {
   
    padding: 20px;
    text-align: center;
    color: #555;
  }
  
  form {
    max-width: 400px;
    margin: 0 auto;
    background-color: #3498db; /* Background color for the form */
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  /* Styling for form elements */
  form input,
  form textarea,
  form button {
    width: 100%;
    padding: 10px;
    margin-bottom: 16px;
    box-sizing: border-box;
    border: none;
    border-radius: 5px;
  }
  
  form button {
    background-color: #297fb8; /* Button background color */
    color: #fff;
    padding: 12px;
    cursor: pointer;
    font-size: 1.2em;
    border: 1px solid #297fb8; /* Border color */
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out; /* Smooth transition on hover */
  }
  
  form button:hover {
    background-color: #245e88; /* Darker color on hover */
  }
  